<template><section><h1>Input 输入框</h1>
<template>
    <div class="inputs">
        <coding title="标准" content="简单的输入框。" :code="code">
            <y-input placeholder="asdf"></y-input>
        </coding>
        <coding title="大小" content="有三种大小，默认、lg、sm。" :code="code2">
            <y-input placeholder="default"></y-input>
            <y-input placeholder="lg" size="lg"></y-input>
            <y-input placeholder="sm" size="sm"></y-input>
        </coding>
        <coding title="标签" content="用于配置一些固定组合。" :code="code">
            <y-input placeholder="default" addon-before="asdf"></y-input>
            <y-input placeholder="default" addon-before="asdf" addon-after="asdf"></y-input>
        </coding>
    </div>
</template>


<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>placeholder</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>addon-before</td>
<td>前标签</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>addon-after</td>
<td>后标签</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less">
.inputs {
    .y-input-wrapper {
        margin-bottom: 16px;
    }
}
</style>
<script>
export default {
    data () {
        return {
            code: `<y-input placeholder="asdf"></y-input>`
        }
    }
}
</script>


